import React, { Component } from 'react';
import request from 'superagent';
import store from './MyStore.js';
import { connect } from 'react-redux';
import FileUploader from './FileUploader.js';

import {Button, ButtonDropdown, DropdownItem, DropdownToggle, DropdownMenu} from 'reactstrap';
import Pagination from 'react-js-pagination';

const mapStateToProps = (state) => {
  console.log("mapStateToProps", state);
  return {tracks: state.tracks,
          music_view_loading_overlay: state.music_view_loading_overlay};
}

const mapDispatchToProps = (dispatch) =>{
  return { updateTracks: (tracks) => dispatch({type:"REFRESH_MUSIC_TRACKS", tracks: tracks}) };
}

