class MusicView extends Component
{

  constructor(props)
  {
    super(props);

    this.state = this.getInitialState();

    this.state = {filter:"",
                  tracks: props.tracks,
                  activePage: 1,
                  itemsCountPerPage: 12};
  }

  getInitialState()
  {
    return({filter:""});
  }

  filterList()
  {
    var filter = this.state.filter;
    var items = this.props.tracks;

    console.log("renderList:", items, filter);
    if(items == undefined)
    {
      console.log("NOT MUSIC ITEMS TO CREATE", this.props.tracks);
      return;
    }

    var filteredItems = items;

    if(filter != undefined || filter != "")
    {
      filteredItems = items.filter(function(item){
        //searching without case sensitivity making both strings lowercase
        return (item.artist.toLowerCase().includes(filter.toLowerCase()))
      });
    }

    var that = this;

    var handleFilter = function(item, index)
    {
      var array = [];

      var maxPerPage = that.state.itemsCountPerPage;
      var activePage = that.state.activePage-1;
      var startIndex = (activePage) * maxPerPage;
      var endIndex = (activePage *maxPerPage) + maxPerPage;

      var maxIndex = that.props.tracks.length;
      console.log("startIndex:", startIndex, "maxIndex:", maxIndex, "endIndex:", endIndex);

      if(index < endIndex && index >= startIndex)
      {
        array.push(<MusicItem key={""+index} data={item} rowNumber={index} audioFileName={item.uri}/>);
      }
      return array;
    }

    var listItems = filteredItems.map(handleFilter);

    return listItems;
  }

  onChangeFilter(event)
  {
    var searchQuery =  event.target.value;

    //event.preventDefault();
    //console.log("value:",searchQuery);
    this.setState({filter: searchQuery, activePage: 1});
  }

  refreshMusic()
  {
    store.dispatch({type:"OVERLAY_MUSIC_VIEW", music_view_loading_overlay: true});

    //curl -X GET "https://us-central1-lustrous-spirit-170901.cloudfunctions.net/song-getAll" -H "accept: application/json"
    const GET_MUSIC_ALL = "https://us-central1-lustrous-spirit-170901.cloudfunctions.net/song-getAll";

    let upload = request.get(GET_MUSIC_ALL)
           .set('Accept', 'application/json');

    upload.on('progress', event => {

    console.log(event.percent);
      /* the event is:
      {
      direction: "upload" or "download"
      percent: 0 to 100 // may be missing if file size is unknown
      total: // total file size, may be missing
      loaded: // bytes downloaded or uploaded so far
      } */
    });

    upload.end((err, response) => {
      
      if (err) 
      {
        console.log("ERROR CAUGHT - response: ", response);
        throw console.error(err);
        return;
      }

      var responseBody = null;

      console.log("response: ",response, err);

      if(response)
      {
        if(response.hasOwnProperty("body"))
        {
          responseBody = response.body;

          //check to see if there was an error
          if(responseBody.hasOwnProperty("error"))
          {
            //if so alert it and abort anything else
            alert(response.body.error + "\n" + response.body.error_description);
            return;
          }
        }
      } 

      //check access denied
      if(response.status == 403)
      {
        throw console.error("request failed");
      }

      if(response.status == 200)
      {
        console.log("Request SUCCESSFUL");
        store.dispatch({type:"OVERLAY_MUSIC_VIEW", music_view_loading_overlay: false});

        var tracks = response.body.tracks;
        this.props.updateTracks(tracks);
        //store.dispatch({type: "REFRESH_MUSIC_TRACKS", tracks: tracks});
      } 

      if(response == undefined)
      {
        throw console.error("response was undefined", err, response);
        return;
      }
    });
  }

  createSearchBox()
  {
    return (<div className="music-search-box"><input placeholder="search" onChange={this.onChangeFilter.bind(this)}/> </div>);
  }

  createMusicButton()
  {
    return(<div color="primary" className="btn-refresh" onClick={this.refreshMusic.bind(this)}><div className="icon-refresh"></div></div>)
  }

   handlePageChange(pageNumber) {
    console.log(`active page is ${pageNumber}`);
    this.setState({activePage: pageNumber});
  }

  createPagination(itemCount)
  {
    return(
        <div className="pagination-container">
        <Pagination
          // hideDisabled
          // prevPageText='Prev'
          // nextPageText='Next'
          // firstPageText='First'
          // lastPageText='Last'

          activePage={this.state.activePage}
          itemsCountPerPage={this.state.itemsCountPerPage}
          totalItemsCount={itemCount}
          pageRangeDisplayed={5}
          onChange={this.handlePageChange.bind(this)}
        />
      </div>
      );
  }

  render(){
    var searchBox = this.createSearchBox();
    var musicButton = this.createMusicButton();

    var musicOverlayClass = this.props.music_view_loading_overlay == false ? " hide" : "";

    var itemList = this.filterList();

    var paginationBox = this.createPagination(itemList.length);

    console.log(">>>>>>RENDER MUSIC:", this.state.filteredItems)
    return(
      <ul className="music-item-view">

        {musicButton}
        {searchBox}
        {paginationBox}
        {itemList}

         <div className={"music-view-loading-overlay-container"+musicOverlayClass}>
          <div className="pink-spiner"></div>
        </div>
      </ul>);
    }
}

class MusicItem extends Component
{

  constructor(props)
  {
    super(props);
    //props.audio = new Audio();
    //var audioObject = new Audio();
    var error = false;
    var that = this;

            var audioObject = new Audio(this.props.audioFileName);
        var that  = this;
        audioObject.addEventListener("error", function(err){
          console.log(">>>> AUDIO ERROR", err);
          var error = true;
          that.setState({error: error});
        });

  this.toggle = this.toggle.bind(this);

    this.state = {audio:audioObject,
                  playingAudio: false, 
                  error: false,
                  dropdownOpen: false};
  }

  compomentWillDismount()
  {
    console.log("<><><><><>< WILL DISMOUNT");
  }

  handleClick()
  {
    if(this.state.error)
    {
      console.warn("error with this audio file, will not attemp to play");
      return;
    }

    if(this.isPlaying(this.state.audio))
    {
      console.log("audio stopping");
      this.state.audio.pause();
      this.state.audio.currentTime = 0.0;
      this.setState({playingAudio: false});
    } else {
      console.log("attempting to audio playing",this.state.audio);

      if(this.state.audio)
      {
        if(this.state.audio.src != "")
        {
          this.state.audio.play();
          this.setState({playingAudio: true});
        }
      } else{
        alert("cannot play file");
      }
    }
  }

  isPlaying(audio)
  {
    console.log("audio.paused", audio.paused, audio.currentTime);

    if(!audio.paused && audio.currentTime > 0)
    {
      console.log("audio is playing");
      return true;
    }
    console.log("audio NO playing");
    return false;
  }

  stopAudio()
  {

  }

  toggle() {
    this.setState({
      dropdownOpen: !this.state.dropdownOpen
    });
  }

  createItem(item)
  {
    var backgroundColors = ["#d2d2d2","#e2e2e2"];
    var mod = this.props.rowNumber  % 2;

    var style = {backgroundColor:backgroundColors[mod]};

    var errorClass = this.state.error ? " audio-error" : "";
    //console.log("ERROR CLASS CREATE");

    var iconClassName = "music-item-thumbnail";
    iconClassName += errorClass;

    if(this.state.playingAudio) iconClassName = "music-item-thumbnail-stop";

    return (<li className="music-item" style={style}>
              <p>{this.props.rowNumber+1}.</p>
              <div className="music-item-body">
                <div className={iconClassName} onClick={this.handleClick.bind(this)}></div>
                <p className="music-item-title">{item.artist}</p>
                <p className="music-item-description">{item.title}</p>
                <ButtonDropdown color="primary" isOpen={this.state.dropdownOpen} toggle={this.toggle} >
                  <DropdownToggle className="music-item-dropdown">{<Dots />}</DropdownToggle>
                  <DropdownMenu right>
                    <DropdownItem color="warning">remove</DropdownItem>
                  </DropdownMenu>
                  </ButtonDropdown>
              </div>
            </li>);
  }

  render()
  {
    return(this.createItem(this.props.data));
    
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(MusicView);


