import React, { Component } from 'react';
import request from 'superagent';
import store from './MyStore.js';
import { connect } from 'react-redux';
import VideoUploader from './VideoUploader.js';

import {Button} from 'reactstrap';
import Pagination from 'react-js-pagination';

const mapStateToProps = (state) => {
  console.log("mapStateToProps", state);
  return {tracks_video: state.tracks_video,
          video_view_loading_overlay: state.video_view_loading_overlay};
}

const mapDispatchToProps = (dispatch) =>{
  //console.log("Dispatch maped", dispatch);
  return { updateVideoTracks: (tracks_video) => dispatch({type:"REFRESH_VIDEO_TRACKS", tracks_video: tracks_video}) };
}

class VideoView extends Component
{

  constructor(props)
  {
    super(props);
    this.state = {filter:"",
                  activePage: 1,
                  filteredItems: [],
                  itemsCountPerPage: 12,
                  error: false};
  }

  renderList()
  {
    var filter = this.state.filter;
    var items = this.props.tracks_video;

    console.log("renderList:", items, filter);
    if(items == undefined)
    {
      console.log("no video ITEMS TO CREATE", this.props.tracks_video);
      return;
    }

    //console.log("render list called", items, filter);

    var filteredItems = items;

    if(filter != undefined || filter != "")
    {
      filteredItems = items.filter(function(item){

        var searchProperty = item.videoURL;
        //searching without case sensitivity making both strings lowercase
        return (searchProperty.toLowerCase().includes(filter.toLowerCase()))
      });
    }


    var that = this;
    //this.setState({pageNumber: totalPages });
    var handleFilter = function(item, index)
    {
      var array = [];

      var maxPerPage = that.state.itemsCountPerPage;
      var activePage = that.state.activePage-1;
      var startIndex = (activePage) * maxPerPage;
      var endIndex = (activePage *maxPerPage) + maxPerPage;

      var maxIndex = that.props.tracks_video.length;
      console.log("startIndex:", startIndex, "maxIndex:", maxIndex, "endIndex:", endIndex);

      if(index < endIndex && index >= startIndex)
      {
        array.push(<VideoItem key={""+index} data={item} rowNumber={index} audioFileName={item.uri}/>);
      }
      return array;
    }

    var listItems = filteredItems.map(handleFilter);

    //var listItems = filteredItems.map((item, index) => <VideoItem key={""+index} data={item} rowNumber={index} audioFileName={item.uri}/>);

    return listItems;
  }

  onChangeFilter(event)
  {
    var searchQuery =  event.target.value;

    //event.preventDefault();
    this.setState({filter: searchQuery, activePage:1});
  }

  refreshvideo()
  {
    store.dispatch({type:"OVERLAY_VIDEO_VIEW", video_view_loading_overlay: true});

    //curl -X GET "https://us-central1-lustrous-spirit-170901.cloudfunctions.net/song-getAll" -H "accept: application/json"
    const GET_ALL_VIDEOS = "https://us-central1-lustrous-spirit-170901.cloudfunctions.net/video-getAll";

    let upload = request.get(GET_ALL_VIDEOS)
           .set('Accept', 'application/json');

    upload.on('progress', event => {

    console.log(event.percent);
      /* the event is:
      {
      direction: "upload" or "download"
      percent: 0 to 100 // may be missing if file size is unknown
      total: // total file size, may be missing
      loaded: // bytes downloaded or uploaded so far
      } */
    });

    upload.end((err, response) => {
      
      if (err) 
      {
        console.log("ERROR CAUGHT - response: ", response);
        throw console.error(err);
        return;
      }

      var responseBody = null;

      console.log("response: ",response, err);

      if(response)
      {
        if(response.hasOwnProperty("body"))
        {
          responseBody = response.body;

          //check to see if there was an error
          if(responseBody.hasOwnProperty("error"))
          {
            //if so alert it and abort anything else
            alert(response.body.error + "\n" + response.body.error_description);
            return;
          }
        }
      } 

      //check access denied
      if(response.status == 403)
      {
        throw console.error("request failed");
      }

      if(response.status == 200)
      {
        console.log("Request SUCCESSFUL");
        store.dispatch({type:"OVERLAY_VIDEO_VIEW", video_view_loading_overlay: false});

        var videos = response.body.videos;
        this.props.updateVideoTracks(videos);
        //store.dispatch({type: "REFRESH_video_TRACKS", tracks: tracks});
      } 

      if(response == undefined)
      {
        throw console.error("response was undefined", err, response);
        return;
      }
    });
  }

  createSearchBox()
  {
    return (<div className="video-search-box"><input placeholder="search" onChange={this.onChangeFilter.bind(this)}/> </div>);
  }

  createvideoButton()
  {
    return(<div color="primary" className="btn-refresh" onClick={this.refreshvideo.bind(this)}><div className="icon-refresh"></div></div>)
  }

  handlePageChange(pageNumber) {
    console.log(`active page is ${pageNumber}`);
    this.setState({activePage: pageNumber});
  }

  createPagination(totalCount)
  {
    return(
        <div className="pagination-container">
        <Pagination
          //hideDisabled
          //prevPageText='Prev'
          //nextPageText='Next'
          //firstPageText='First'
          //lastPageText='Last'

          activePage={this.state.activePage}
          itemsCountPerPage={this.state.itemsCountPerPage}
          totalItemsCount={totalCount}
          pageRangeDisplayed={5}
          onChange={this.handlePageChange.bind(this)}
        />
      </div>
      );
  }

  render(){
    var searchBox = this.createSearchBox();
    var videoButton = this.createvideoButton();

    var videoOverlayClass = this.props.video_view_loading_overlay == false ? " video-hide" : "";

    var itemList = this.renderList(this.props.tracks_video, this.state.filter);
    var paginationControlls = this.createPagination(itemList.length);

    console.log(" >>>>>>> VIDEO PROPS,", this.props);

    return(
      <ul className="video-item-view">
        {videoButton}
        {searchBox}
        {paginationControlls}
        {itemList}

        <div className={"video-view-loading-overlay-container"+videoOverlayClass}>
          <div className="pink-spiner"></div>
        </div>
      </ul>);
    }
}

